          <blockquote style="margin-top:-20px" class="blockquote">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Green Arrow最初并不是漫画迷之外的知名角色：他曾出演过1973年动画系列Super Friends的一集。在2000年代，这个角色出现在一些DC电视属性中，包括动画系列正义联盟无限，年轻正义，蝙蝠侠和蝙蝠侠：勇敢和大胆，以及几个DC宇宙动画原创电影。在现场表演中，他出演了由演员贾斯汀哈特利饰演的Smallville系列剧，并成为了一名核心演员。2012年，真人连续剧“ 箭头”在CW上首次亮相其中标题人物由Stephen Amell描绘，并推出了几个衍生系列，成为DC漫画共享电视世界的起点，称为Arrowverse。</blockquote>
        
